CA,qBAAA;CACA;AACA;EACA,WAAA;CACA;AACA;EACA,WAAA;EACA,sBAAA;CACA","file":"productList.vue","sourcesContent":["<template>\n  <div class='product'>\n    <ctbHead>\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\n                   v-for=\"item in authList\"\n                   :key=\"item.id\"\n                   :to=\"{path:item.action,query:{id:curId}}\"\n                   :v-key=\"item.id\">\n        {{item.name}}\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\">\n          <i></i>\n          <span>会员特权服务项</span>\n          <el-button class='add'\n                     type=\"primary\"\n                     @click=\"addState=1,add()\">添加</el-button>\n        </div>\n        <div class=\"quote-nav\">\n          <router-link :class=\"thCurId==item.id? 'cur':''\"\n                       v-for=\"item in threeAuthList\"\n                       :key=\"item.id\"\n                       :to=\"{path:item.action,query:{id:curId}}\">\n            {{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <div>\n        <table class='table '>\n          <tr>\n            <th>服务项名称</th>\n            <th>产品名称</th>\n            <th>产品图片</th>\n            <th>产品规格</th>\n            <th>产品数量</th>\n            <th>更换周期</th>\n            <th>后续折扣</th>\n            <th>创建时间</th>\n            <th>操作</th>\n          </tr>\n          <tr v-for=\"item of list\">\n            <td>{{item.ser_name}}</td>\n            <td>{{item.name}}</td>\n            <td>\n              <img class='image'\n                   :src=\"item.image\"\n                   @click=\"imgDialogVisible=true,dialogImageUrl=item.image\">\n            </td>\n            <td>{{item.size}}</td>\n            <td>{{item.number}}</td>\n            <td>{{item.period}}</td>\n            <td>{{item.redate}}</td>\n            <td>{{item.create_time}}</td>\n            <td>\n              <a href=\"javascript:;\"\n                 @click=\"details(item.content)\"\n                 class='col-1EA'>详情</a>\n              <a href=\"javascript:;\"\n                 class='col-0ACD00'\n                 @click=\"addState=2,addDialogVisible=true,modify(item.id,item.pid,item.ser_name,item.name,item.image,item.size,item.number,item.period,item.redate,item.content)\">修改</a>\n              <a href=\"javascript:;\"\n                 class='col-FA644B'\n                 @click=\"del(item.id,item.ser_name)\">删除</a>\n            </td>\n          </tr>\n        </table>\n      </div>\n      <!--详情弹框-->\n      <el-dialog title=\"详情\"\n                 :visible.sync=\"detailsDialogVisible\"\n                 width=\"30%\"\n                 center>\n        <div class='details'>\n          <p>产品描述:</p>\n          <p v-html='describe'></p>\n        </div>\n      </el-dialog>\n      <!--添加修改弹框-->\n      <el-dialog :title=\"addState==1?'添加':'修改'\"\n                 :visible.sync=\"addDialogVisible\"\n                 width=\"800px\"\n                 center>\n        <div class=\"addDialog\">\n          <el-form ref=\"form\"\n                   :model=\"form\"\n                   label-width=\"100px\">\n            <el-form-item label=\"服务项名称 : \">\n              <el-select placeholder=\"请选择服务项\"\n                         v-model=\"form.pid\">\n                <el-option v-for=\"item of selsetList\"\n                           :label=\"item.ser_name\"\n                           :key=\"item.id\"\n                           :value=\"item.id\"></el-option>\n              </el-select>\n            </el-form-item>\n            <el-form-item label=\"产品名称 : \">\n              <el-input placeholder=\"请选择产品名称\"\n                        v-model=\"form.name\"></el-input>\n            </el-form-item>\n            <el-form-item label=\"产品图片 : \">\n              <el-upload class=\"upload\"\n                         action=\"https://ceshi.ctbls.com/admin/SystemSetup/proImg\"\n                         name='image'\n                         :data=\"{token:token}\"\n                         list-type=\"picture-card\"\n                         :on-success=\"handlesuccess\"\n                         :file-list='form.imgArr'\n                         :on-preview=\"handlePictureCardPreview\"\n                         :on-remove=\"handleRemove\">\n                <i class=\"el-icon-plus\"></i>\n              </el-upload>\n              <el-dialog :visible.sync=\"dialogVisible\"\n                         append-to-body>\n                <img width=\"100%\"\n                     :src=\"dialogImageUrl\"\n                     alt=\"\">\n              </el-dialog>\n            </el-form-item>\n            <el-form-item label=\"产品规格 : \">\n              <el-input v-model=\"form.size\"\n                        placeholder=\"件/升/个\"></el-input>\n            </el-form-item>\n            <el-form-item label=\"产品数量 : \">\n              <el-input v-model=\"form.number\"\n                        placeholder=\"请输入产品数量\"></el-input>\n            </el-form-item>\n            <el-form-item label=\"更换周期 : \">\n              <el-input v-model=\"form.period\"\n                        placeholder=\"请输入周期\"></el-input>\n            </el-form-item>\n            <el-form-item label=\"后续折扣 : \">\n              <el-input v-model=\"form.redate\"\n                        placeholder=\"请输入折扣\"></el-input>\n            </el-form-item>\n            <el-form-item label=\"产品描述 : \">\n              <wangEditor class=\"wangEditor\"\n                          :catchData=\"catchData\"\n                          :content='form.content'></wangEditor>\n            </el-form-item>\n          </el-form>\n        </div>\n        <span slot=\"footer\"\n              class=\"dialog-footer\">\n          <el-button type=\"primary\"\n                     @click=\"Submission\">{{addState==1?'添加':'修改'}}</el-button>\n        </span>\n      </el-dialog>\n      <!--图片弹框-->\n      <el-dialog :visible.sync=\"imgDialogVisible\"\n                 width=\"30%\"\n                 center>\n        <div>\n          <img width=\"100%\"\n               :src=\"dialogImageUrl\"\n               alt=\"\">\n        </div>\n      </el-dialog>\n      <!-- 分页 -->\n      <div class=\"page_center\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n\nimport wangEditor from \"@/components/common/wangEditor\"\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      page: 1, //显示的页码数\n      pageCount: 1,\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      addDialogVisible: false,  //添加弹框状态\n      detailsDialogVisible: false,//详情弹框状态\n      imgDialogVisible: false, //图片弹框状态\n      DialogState: 1, //控制弹框的状态\n      currentID: 0,//当前列表id\n      dialogImageUrl: '',\n      dialogVisible: false,\n      selsetList: [],\n      form: {\n        token: window.sessionStorage.getItem('bbytoken'),\n        pid: '',\n        name: '',\n        size: '',\n        image: '',\n        number: '',\n        period: '',\n        redate: '',\n        content: ''\n      },\n      describe: '', //产品描述\n      addState: 1,\n      imgArr: []\n    }\n  },\n  components: { wangEditor },\n  created () {\n    this.init();\n\n  },\n  filters: {\n    Removelabel (e) {\n      return e.replace(/<[^>]+>/g, \"\");\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/SystemSetup/proItem', { token: this.token, page: this.page })\n        .then(res => {\n\n          if (res.data.code == 1) {\n            this.list = res.data.data.list\n            var data = res.data.data;\n            this.pageCount = res.data.data.rows;\n          } else {\n            this.list = []\n            layer.msg(res.data.msg, { time: 1500 })\n          }\n        }).catch(err => { })\n    },\n    //点击添加\n    add () {\n      this.addDialogVisible = true\n      this.selsetData()\n    },\n    selsetData () {\n      this.$axios.post('admin/SystemSetup/serItem', { token: this.token }).then(res => {  //获取服务项列表\n        if (res.data.code == 1) {\n          console.log(res)\n          this.selsetList = res.data.data;\n        }\n      }).catch(err => console.log(err))\n    },\n    handleRemove (file, fileList) {  //删除图片\n      this.form.image = ''\n    },\n    handlePictureCardPreview (file) {  //图片放大\n      this.dialogImageUrl = file.url;\n      this.dialogVisible = true;\n    },\n    handlesuccess (res) {  //图片上传成功,获取图片路径\n      this.form.image = res;\n    },\n    catchData (val) {  //获取富文本内容\n      console.log(val)\n      this.form.content = val;\n    },\n    Submission () { //提交\n      this.$message.error('123')\n      console.log(this.form)\n      this.$axios.post('admin/SystemSetup/addPro', this.form).then(res => {\n        console.log(res)\n        if (res.data.code == 1) {\n          layer.msg(res.data.msg, { time: 1500 }, (() => {\n            this.addDialogVisible = false\n            this.init()\n          }))\n        } else {\n          layer.msg(res.data.msg, { time: 1500 })\n        }\n      }).catch(err => console.log(err))\n    },\n    details (content) {\n      this.detailsDialogVisible = true\n      this.describe = content\n\n    },\n    modify (id, pid, ser_name, name, image, size, number, period, redate, content) { //修改\n      this.selsetData()\n      console.log(this.form)\n      this.form = {\n        token: this.token,\n        pid: pid,\n        id: id,\n        name: name,\n        imgArr: [{ url: image }],\n        image: image,\n        size: size,\n        number: number,\n        period: period,\n        redate: redate,\n        content: content\n      }\n    },\n    del (id, ser_name) { //删除\n      this.$confirm('此操作将永久删除次产品吗, 是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning',\n      }).then(() => {\n        this.$axios.post('admin/SystemSetup/prodel', { token: this.token, id: id, name: ser_name }).then(res => {\n          if (res.data.code == 1) {\n            layer.msg(res.data.msg, { time: 1000 }, (() => {\n              this.init()\n            }))\n          } else {\n            layer.msg(res.data.msg, { time: 1000 })\n          }\n        }).catch(err => console.log(err))\n      }).catch(() => { })\n    }\n  },\n  mounted () {\n    console.log(this.list)\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == '半价服务') {\n              this.seCurId = arr[i].id;\n              this.threeAuthList = arr[i].son;\n              console.log(arr[i])\n              for (var j = 0; j < arr[i].son.length; j++) {\n                console.log(arr[i].son[j].name)\n                if (arr[i].son[j].name == \"产品列表\") {\n                  this.thCurId = arr[i].son[j].id\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n}\n</script>\n<style scoped>\n.demo-table-expand {\n  font-size: 0;\n}\n.demo-table-expand label {\n  width: 90px;\n  color: #99a9bf;\n}\n\n.addbtn {\n  float: right;\n  margin-right: 100px;\n  margin-top: 20px;\n}\n.tree:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n.tree > li {\n  float: left;\n\n  box-sizing: border-box;\n  text-align: center;\n  font-size: 16px;\n  color: #8a8d93;\n  line-height: 